body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.link {
  color: #fff;
  background-color: #db7093;
  border: none;
  border-radius: 5px;
  margin: 15px 0 0 15px;
  padding: 10px 15px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: #000;
  background-color: #00f;
}

.js-card-conteiner {
  margin: 15px 0 0 25px;
}

.card-img-top {
  width: 150px;
  height: 150px;
}

img {
  width: 100%;
  display: inline-block;
}

.form-submite {
  margin: 15px 0 0 25px;
  display: flex;
}

.input-submite {
  padding: 10px 15px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

.bitton-submite {
  color: #fff;
  border: inherit;
  cursor: pointer;
  background-color: #5a639f;
  border-radius: 0 5px 5px 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

.bitton-submite:hover, .btn:hover {
  color: #000;
  background-color: tomato;
}

.btn {
  color: #fff;
  border: inherit;
  cursor: pointer;
  background-color: #5a639f;
  border-radius: 5px;
  margin: 15px 0 0 25px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

.articles {
  margin-top: 40px;
}

.articles-item {
  width: 400px;
  border: 1px solid #000;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 5px;
  list-style-type: none;
}

.articles-link {
  color: #000;
  text-decoration: none;
}

.articles-img {
  width: 100%;
  display: inline-block;
}

.articles-title {
  margin-top: 10px;
}

